<template>
<section class="section">
  <div class="container">
    <div>
      <form class="form-inline" @submit.prevent="addTicker">
        <div class="field has-addons">
          <div class="control">
            <input type="text" class="input" v-model="input" placeholder="Ticker">
          </div>
          <div class="control">
            <button type="submit" class="button is-primary">Add</button>
          </div>
        </div>
      </form>
    </div>
    <hr>
    <div class="columns is-multiline" v-if="tickers && tickers.length">
      <div class="column is-6" v-for="(ticker, index) in tickers" :key="'ticker_' + ticker">
        <div class="box">
          <div class="title is-6">
            <div class="flex-v-center">
              <span>
                <span :id="ticker + '_name'"></span>
                <br>
                <span class="is-size-7">{{ ticker }}</span>
              </span>
              <span class="is-size-4" :id="ticker + '_price'"></span>
              <span class="tag" :id="ticker + '_diff'"></span>
              <span class="delete is-pulled-right" @click="removeTicker(index)"></span>
            </div>
            </div>
          <hr>
          <div class="columns">
            <div class="column is-6">
              <img :src="'https://image.sinajs.cn/newchart/hk_stock/min/' + ticker + '.gif?' + timestamp"  class="clickable" @click="popUp(ticker, 'min')" alt="">
            </div>
            <div class="column is-6">
              <img :src="'https://image.sinajs.cn/newchart/hk_stock/daily/' + ticker + '.gif?' + timestamp"  class="clickable" @click="popUp(ticker, 'daily')" alt="">
            </div>
            </div>
          <script2 :src="'https://hq.sinajs.cn/list=hk' + ticker"></script2>
          <script2>
            var l_{{ticker}} = hq_str_hk{{ticker}}.split(',');
            document.getElementById('{{ticker}}_name').innerHTML = l_{{ticker}}[1];
            document.getElementById('{{ticker}}_price').innerHTML = l_{{ticker}}[6];
            document.getElementById('{{ticker}}_diff').innerHTML = l_{{ticker}}[8] + '%';
            if (parseFloat(l_{{ticker}}[8]) > 0) {
              document.getElementById('{{ticker}}_diff').className = 'tag is-success';
              document.getElementById('{{ticker}}_price').className = 'is-size-3 has-text-success';
            } else if (parseFloat(l_{{ticker}}[8]) < 0) {
              document.getElementById('{{ticker}}_diff').className = 'tag is-danger';
              document.getElementById('{{ticker}}_price').className = 'is-size-3 has-text-danger';
            } else {
              document.getElementById('{{ticker}}_diff').className = 'tag';
              document.getElementById('{{ticker}}_price').className = 'is-size-3';
            }
          </script2>
        </div>
      </div>
    </div>
  </div>
  <div class="modal" :class="{ 'is-active' : pop }">
    <div class="modal-background"></div>
    <div class="modal-content">
      <img :src="popUrl" alt="">
    </div>
    <button class="modal-close is-large" aria-label="close" @click="pop=false"></button>
  </div>
</section>
</template>
<script>
import Cookies from 'js-cookie'
import moment from 'moment'

export default {
  name: 'Quote',
  data() {
    return {
      input: '',
      tickers: ['00700'],
      pop: false,
      popUrl: '',
      timestamp: moment().format('HHmm')
    }
  },
  created() {
    this.intervalId = setInterval(() => {
      this.timestamp = moment().format('HHmm')
    }, 1000 * 10)
  },
  destroyed() {
    clearInterval(this.intervalId)
  },
  mounted() {
    this.checkIfCookie()
    this.updateQuotes()
  },
  methods: {
    updateQuotes() {
      // console.log('loading')
      let vm = this
      this.$forceUpdate()
      setTimeout(function () {
        vm.updateQuotes()
      }, 1000 * 60)
    },
    addTicker() {
      if (!this.input || this.input === '') {
        return
      }
      if (this.input !== 'hsi') {
        this.input = this.input.padStart(5, '0')
      }
      this.tickers.push(this.input)
      this.input = ''
      this.$forceUpdate()
      Cookies.set('tickers_list', JSON.stringify(this.tickers), { expires: 7 })
      // console.log('set cookie', JSON.stringify(this.tickers))
    },
    removeTicker(index) {
      console.log('del', index)
      this.tickers.splice(index, 1)
      this.$forceUpdate()
      Cookies.set('tickers_list', JSON.stringify(this.tickers), { expires: 7 })
      // console.log('set cookie', JSON.stringify(this.tickers))
    },
    popUp (ticker, type) {
      this.pop = true
      this.popUrl = 'https://image.sinajs.cn/newchart/hk_stock/' + type + '/' + ticker + '.gif?' + this.timestamp
    },
    checkIfCookie () {
      const cookie = Cookies.get('tickers_list')
      // console.log('cookie', cookie)
      if (cookie) {
        this.tickers = JSON.parse(cookie)
      }
    }
  }
}
</script>

<style>
  .clickable {
    cursor: pointer; 
  }
  .modal-content img {
    background-color: white;
  }
  .flex-v-center {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
</style>
